<template>
  <div>
      <div id="myChart" style="width: 500px;height: 300px;"></div>
  </div>
</template>

<script>
  import {axioPost} from "../utils/httpUtil";
  import api from "../utils/api";

  export default {
    name: "stat",
    data() {
      return {
        count: 0,
        titleList:[],
        countList:[],
      }
    },
    mounted(){
      this.getCount();
    },
    methods:{
      async getCount(){
        let result = await axioPost(api.countUrl,{});
        console.log(result);
        if (result.code==200){
          this.count = result.data.count;
          this.formatData(result);

          this.initChart();
        }

      },
      //初始化
      initChart(){
        // 基于准备好的dom，初始化echarts实例
        let myChart = this.$echarts.init(document.getElementById('myChart'))
        // 绘制图表

        myChart.setOption({
          title: { text: '打开总次数：'+this.count+ "次"},
          tooltip: {},
          xAxis: {
            data: this.titleList
          },
          yAxis: {},
          series: [{
            name: '次数',
            type: 'bar',
            data: this.countList
          }]
        });
      },
      formatData(result){
        let list = result.data.list;
        for (let item of list){
          console.log(item);
          this.titleList.push(item.date);
          this.countList.push(item.count);
        }
      }
    }
  }
</script>

<style scoped>

</style>



