import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import login from "@/views/login.vue"
import main from "@/views/main.vue"
import user from "@/views/user.vue"
import stat from "@/views/stat.vue"

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/helloworld',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path:"/",
      name:"login",
      component:login
    },
    {
      path:"/main",
      name:"main",
      component:main,
      redirect: "/user/index"
    },
    {
      path:"/user",
      name:"user",
      component:main,
      children:[
        {
          path:"index",
          name:"user_index",
          component:user,
        }
      ]
    },
    {
      path:"/stat",
      name:"stat",
      component:main,
      children:[
        {
          path:"index",
          name:"stat_index",
          component:stat,
        }
      ]
    }
  ]
})



