<template>
  <div>
    <el-scrollbar wrap-class="scrollbar-wrapper">
      <el-menu
        default-active="1"
        class="el-menu-vertical-demo"
        @open="handleOpen"
        @close="handleClose"
        background-color="#545c64"
        text-color="#fff"
        active-text-color="#ffd04b">
        <el-menu-item index="1" @click="openPage('user_index')">
          <i class="el-icon-menu"></i>
          <span slot="title">用户分析</span>
        </el-menu-item>
        <el-menu-item index="2" @click="openPage('stat_index')">
          <i class="el-icon-document"></i>
          <span slot="title">流量统计</span>
        </el-menu-item>
        <el-submenu index="3">
          <template slot="title">
            <i class="el-icon-location"></i>
            <span>素材管理</span>
          </template>
          <el-menu-item-group>
            <el-menu-item index="1-1">素材列表</el-menu-item>
            <el-menu-item index="1-2">上传素材</el-menu-item>
          </el-menu-item-group>
        </el-submenu>
        <el-menu-item index="4">
          <i class="el-icon-setting"></i>
          <span slot="title">导航四</span>
        </el-menu-item>
      </el-menu>
    </el-scrollbar>
  </div>
</template>

<script>
  export default {
    name: "SideMenu",
    methods: {
      openPage(name) {
        this.$router.push({
          name:name
        });
      },
      handleOpen(key, keyPath) {
        console.log(key, keyPath);
      },
      handleClose(key, keyPath) {
        console.log(key, keyPath);
      }
    }
  }
</script>

<style scoped>
  .el-menu-vertical-demo {
    height: 100vh;
  }
</style>



